  color:white;
}

nav .animation{
  position: absolute;
  height: 5px;
  bottom: 16px;
  z-index: 0;
  background: gold;
  border-radius: 8px;
  transition: all .7s ease 0s;
}

a:nth-child(1){
  width: 130px;
}
nav .start-home,a:nth-child(1):hover~.animation{
  width: 85px;
  left: 10px;
}
